.edc{position:relative;display:flex;justify-content:center;align-items:center;flex-direction:row;flex-wrap:wrap;min-height:auto;overflow:visible;padding:4rem 0;background:var(--green-theme-background-color)}.edc_ctn{display:flex;justify-content:center;align-content:center;flex-direction:column;align-items:center;width:100%;flex:1 1 50%;margin:10vh auto}.edc_ctn_title{color:var(--green-theme-main-color);font-size:6rem;font-weight:800}.edc_ctn_wrp{display:flex;align-content:center;flex-direction:row;align-items:center;width:80%;padding:2rem 4rem;margin:1rem auto;background:hsla(0,0%,100%,.05);border-radius:12px;box-shadow:0 4px 6px rgba(0,0,0,.1);border-left:3px solid var(--green-theme-main-color);transition:all .3s ease}.edc_ctn_wrp:hover{transform:translateX(10px) translateY(-5px);box-shadow:0 8px 12px rgba(0,0,0,.15)}.edc_ctn_wrp_textbox{width:100%}.edc_ctn_wrp_textbox::before{content:"";position:absolute;width:2rem;height:2rem;margin:1.7rem;border-radius:50%;background-color:var(--green-theme-main-color);margin-left:-5.1rem}.edc_ctn_wrp_textbox h3{color:var(--green-theme-main-color);margin:10px 2px;font-weight:400;font-size:2rem;line-height:3rem}.edc_ctn_wrp_textbox span{color:var(--green-theme-sub-dark-text-color);margin:1px 2px;font-size:1.5rem;line-height:2rem;display:block}.edc_ctn_wrp_textbox p{color:var(--green-theme-sub-text-color);margin:10px 2px;font-size:1.5rem;line-height:2rem;white-space:wrap;position:relative}.edc_ctn_wrp_textbox .education-tags{display:flex;flex-wrap:wrap;gap:.5rem;margin-top:1rem}.edc_ctn_wrp_textbox .education-tags .tag{background:var(--green-theme-main-color);color:#fff;padding:.3rem .8rem;border-radius:20px;font-size:1.2rem;opacity:.9}.edc_ctn_wrp_textbox .timeline-indicator{position:absolute;left:-4.5rem;width:3px;height:100%;background:var(--green-theme-main-color);opacity:.3}.edc_ctn_wrp_textbox .achievements{margin-top:1rem;padding-left:1.5rem}.edc_ctn_wrp_textbox .achievements li{color:var(--green-theme-sub-text-color);font-size:1.4rem;margin-bottom:.5rem;position:relative}.edc_ctn_wrp_textbox .achievements li::before{content:"•";position:absolute;left:-1.5rem;color:var(--green-theme-main-color)}.edc_ctn_wrp:nth-child(2){border-left-color:#5b8a72}.edc_ctn_wrp:nth-child(2) .edc_ctn_wrp_textbox::before{background-color:#5b8a72}.edc_ctn_wrp:nth-child(3){border-left-color:#5e88a1}.edc_ctn_wrp:nth-child(3) .edc_ctn_wrp_textbox::before{background-color:#5e88a1}.edc_ctn_wrp:nth-child(4){border-left-color:#a57966}.edc_ctn_wrp:nth-child(4) .edc_ctn_wrp_textbox::before{background-color:#a57966}@media only screen and (max-width: 375px){.edc_ctn_title{font-size:3rem}.edc_ctn_wrp_textbox::before{width:1.5rem;height:1.5rem;margin-left:-4.8rem}.edc_ctn_wrp_textbox h3{font-size:1.5rem}.edc_ctn_wrp_textbox span{font-size:1.25rem}.edc_ctn_wrp_textbox p{font-size:1.25rem}}@media only screen and (min-width: 376px)and (max-width: 768px){.edc_ctn_title{font-size:3rem}.edc_ctn_wrp_textbox::before{width:1.5rem;height:1.5rem;margin-left:-4.8rem}.edc_ctn_wrp_textbox h3{font-size:1.5rem}.edc_ctn_wrp_textbox span{font-size:1.25rem}.edc_ctn_wrp_textbox p{font-size:1.25rem;line-height:2rem}}@media only screen and (min-width: 769px)and (max-width: 1024px){.edc_ctn_title{font-size:4rem}.edc_ctn_wrp_textbox::before{width:1.5rem;height:1.5rem;margin-left:-4.8rem}.edc_ctn_wrp_textbox h3{font-size:1.5rem}.edc_ctn_wrp_textbox span{font-size:1.25rem}.edc_ctn_wrp_textbox p{font-size:1.25rem;line-height:2rem}}@keyframes slideIn{from{opacity:0;transform:translateX(-30px)}to{opacity:1;transform:translateX(0)}}